import React from 'react'

interface Props { name: string }

export default function Welcome ({ name }: Props): JSX.Element {
  const morning = ''
  const noon = ''
  const afternoon = ''
  const night = ''
  const now = new Date().getHours()

  let hitedInfo = ''

  switch (true) {
    case now >= 7 && now <= 12:
      hitedInfo = morning
      break
    case now === 12:
      hitedInfo = noon
      break
    case now >= 13 && now <= 18:
      hitedInfo = afternoon
      break
    case now >= 19 || now <= 6:
      hitedInfo = night
      break
  }
  
  return (
    <span className="sticky__user-info">{`${hitedInfo}${name}`}</span>
  )
}